* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  width: 100%;
  position: fixed;
  background-color: rgba(255, 255, 255, 0);
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-cont {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 10px 20px;
}

.logo-header {
  height: 50px;
  width: 54px;
}

.seccion-opciones {
  width: 36px;
  height: 32px;
  display: grid;
  gap: auto;
}

@media (min-width: 768px) {
  .seccion-opciones {
    display: none;
  }
}

.barras {
  background-color: #141414;
  width: 36px;
  height: 6px;
}

.navegacion {
  display: none;
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    width: 428px;
    height: 28px;
    justify-content: space-between;
  }

  a {
    font-family: "Roboto";
    color: black;
    font-weight: 700;
  }
}

/* MAIN */

.main {
  width: 100%;
  height: 616px;
  display: flex;
  background-color: gainsboro;
  background-image: url("imagenes/puntitos.png");
  background-position: center;
}

.contenedor-main {
  width: 100%;
  display: grid;
  grid-template-rows: 162px 96px 58px 300px;
  justify-items: center;
}

@media (min-width: 768px) {
  .contenedor-main {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding: 0 80px;
  }
}

.texto-principal {
  grid-row: 2;
  font-family: "Roboto";
  font-weight: 700;
  font-size: 48px;
  text-align: center;
  margin: 0 15px;
}

@media (min-width: 768px) {
  .texto-principal {
    font-family: "Roboto";
    font-weight: 700;
    font-size: 48px;
    text-align: center;
    margin: 0;
    align-self: center;
  }
}

.foto-main {
  width: 318px;
  height: 300px;
  grid-row: 4;
}

@media (min-width: 768px) {
  .foto-main {
    width: 318px;
    height: 300px;
  }
}

/* SECCION SOBRE MI*/

.me {
  background-color: #141414;
  height: 538px;
}

@media (min-width: 768px) {
  .me {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.contenedor-me {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 55px;
}

@media (min-width: 768px) {
  .contenedor-me {
    flex-direction: row-reverse;
    max-width: 823px;
    align-items: center;
    gap: 95px;
  }
  .contenedor-me-texto {
    display: flex;
    flex-direction: column;
  }
}

.titulo-me,
.texto-me {
  color: white;
  font-family: "Roboto";
}

.titulo-me {
  font-size: 32px;
  font-weight: 700;
  margin: 50px 0;
  text-align: center;
}

@media (min-width: 768px) {
  .titulo-me {
    text-align: start;
    margin: 0 0 28px 0;
  }
}

.texto-me {
  font-size: 16px;
  font-weight: 400;
}

.check-me {
  width: 80px;
  height: 80px;
  margin: 50px;
}

@media (min-width: 768px) {
  .check-me {
    margin: 0;
    width: 211px;
    height: 211px;
  }
}

/* Mis servicios */

.mis-servicios {
  background-color: #fafafa;
  font-family: "Roboto";
  height: 1375px;
}

@media (min-width: 768px) {
  .mis-servicios {
    height: 604px;
  }
}

.titulo-servicios {
  color: #2c2c2c;
  font-size: 48px;
  font-weight: 700;
  text-align: center;
  margin: 60px 0;
}

.contenedor-servicios {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 104px;
}

@media (min-width: 768px) {
  .contenedor-servicios {
    flex-direction: row;
    justify-content: center;
    gap: 233px;
    margin: 0 30px;
  }
}

.servicios {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 271px;
  height: 312px;
}

.imagen-pc-servicios {
  width: 138px;
  height: 138px;
}

.subtitulo-servicios {
  font-size: 24px;
  font-weight: 700;
  text-align: center;
  margin: 14px 0;
}

.descripcion-servicio {
  font-size: 16px;
  font-weight: 400;
  text-align: center;
  margin: 0;
}

/* Contacto */

.contacto {
  background-color: #141414;
  min-height: 660px;
  font-family: "Roboto";
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 70px;
}

@media (min-width: 768px) {
  .contacto {
    flex-direction: row;
    gap: 77px;
    align-items: flex-start;
  }
}

.texto-contacto {
  color: #fafafa;
  font-size: 32px;
  font-weight: 700;
  margin: 65px 0;
}

@media (min-width: 768px) {
  .texto-contacto {
    margin: 100px 0;
  }
}

.contenedor-formulario {
  display: flex;
  flex-direction: column;
  width: 335px;
}

@media (min-width: 768px) {
  .contenedor-formulario {
    align-self: center;
  }
}

.label-contacto {
  font-family: "Roboto";
  color: #fafafa;
  font-size: 13px;
  margin-bottom: 9px;
}

.input-contacto {
  margin-bottom: 27px;
  width: 335px;
  height: 45px;
}

.mensaje-contacto {
  margin-bottom: 60px;
  width: 335px;
  height: 113px;
}

.boton {
  width: 335px;
  height: 45px;
  border-radius: 5px;
  font-family: "Roboto";
  font-size: 16px;
  font-weight: 700;
  background-color: #ffd953;
}

/* Footer */

.footer {
  width: 100%;
  height: 493px;
  background-color: #bdbdbd;
  font-family: "Roboto";
}

.logo-footer {
  width: 77px;
  height: 71px;
  margin: 10px 30px;
}

.sub-logo-footer {
  font-size: 13px;
  font-weight: 400;
  margin: 10px 30px;
}

.logo-redes-footer {
  width: 29px;
  height: 29px;
  margin: 10px 30px;
  display: flex;
}

@media (min-width: 768px) {
  .footer {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    max-height: 346px;
  }
  .logo-footer-contenedor {
    display: flex;
    flex-direction: column;
  }
}

.red-footer {
  display: flex;
  flex-direction: row;
}
